.product-of-week {
  @include reset-p;

  position: relative;

  margin-bottom: 32px;

  &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 6px;
    background-image: url("../img/bg-zigzag-line.svg");
    left: 0;
    background-repeat: repeat-x;
    background-size: contain;
  }
}

.product-of-week__wrapper {
  padding: 0 30px;
  padding-bottom: 36px;
  margin: 0 auto;
}

.product-of-week__title {
  margin: 0;
  font-size: 25px;
  line-height: 30px;
  font-weight: bold;
  padding-bottom: 5px;
}

.product-of-week__week-item {
  display: block;
  font-size: 17px;
  line-height: 20px;
  color: $blue-color;
  padding-bottom: 23px;
  text-transform: uppercase;
}

.product-of-week__description {
  @include reset-p;

  font-size: 15px;
  line-height: 24px;
  padding-bottom: 18px;
}

.product-of-week__table {
  width: 100%;
  font-size: 15px;
  line-height: 20px;
  color: $grey-color;
  margin-bottom: 28px;

  border-collapse: collapse;

  td {
    padding-right: 21px;
  }

  tr {
    border-bottom: 1px solid #e7e7e7;
  }
}

.product-of-week__option {
  height: 39px;
}

.product-of-week__item-image {
  display: block;
  padding-bottom: 25px;
  margin: 0 auto;
}

.product-of-week__price {
  padding: 0;
  margin: 0;
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
  text-align: center;
  padding-bottom: 28px;
  color: #444444;
}

.product-of-week__button {
  // padding: 18px 85px;
  margin: 0 auto;
  width: 100%;
}

@media (min-width: $width-tablet) {
  .product-of-week__mock-title {
    display: flex;
    justify-content: space-between;
    min-width: 640px;
    padding-bottom: 50px;
    align-items: center;
  }

  .product-of-week__title {
    font-size: 43px;
    line-height: 50px;
    font-weight: bold;
    padding: 0;
  }

  .product-of-week__table {
    margin-bottom: 59px;

    td {
      padding-right: 0;
    }
  }

  .product-of-week__week-item {
    text-align: center;
    display: block;
    width: 75px;
    font-size: 17px;
    line-height: 17px;
    padding: 0;
    padding-top: 10px;
  }

  .product-of-week__wrapper {
    margin: 0;
    padding: 0;
    padding-bottom: 53px;
    width: 333px;

    a,
    table,
    p {
      width: 292px;
    }
  }

  .product-of-week__mock-description {
    display: flex;
    flex-flow: column;
    flex-wrap: wrap;
    position: relative;
  }

  .product-of-week__description {
    font-size: 17px;
    line-height: 30px;
    width: 282px;
    color: #444444;
    padding-bottom: 36px;
  }

  .product-of-week__item-image {
    display: block;
    position: absolute;
    top: 8px;
    left: 335px;
    padding: 0;
  }

  .product-of-week__price {
    padding-bottom: 34px;
    font-size: 20px;
  }

  .product-of-week {
    margin-bottom: 64px;

    &::after {
      height: 15px;
    }
  }

  .product-of-week__button {
    margin: 0;
    padding: 14px 85px;
  }
}

@media (min-width: $width-desktop) {
  .product-of-week {
    margin-bottom: 83px;
    padding-top: 55px;

    &::after {
      width: 1050px;
      height: 23px;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  .product-of-week__wrapper {
    width: 1050px;
    padding-bottom: 73px;
  }

  .product-of-week__item-image {
    display: block;
    position: absolute;
    top: -160px;
    left: 525px;
  }

  .product-of-week__week-item {
    order: -1;
  }

  .product-of-week__mock-title {
    padding-left: 47px;
    justify-content: flex-start;
  }

  .product-of-week__mock-description {
    padding-left: 176px;
    // width: 292px;
  }

  .product-of-week__title {
    font-size: 53px;
    line-height: 57px;
    padding-left: 49px;
    z-index: 2;
  }

  .product-of-week__table {
    width: 282px;
    margin-bottom: 111px;
  }

  .product-of-week__price {
    width: 292px;
  }

  .product-of-week__description {
    padding-bottom: 47px;
  }

  .product-of-week__table td {
    padding-right: 0;
  }
}
